#react,
#js,
#html,
#css,
#sass,
#bootstrap,
#redux,
#mongo,
#express,
#node,
#python,
#django,
#js-test,
#sass-glass {
  transform-origin: center;
  transform-box: fill-box;
}

#react {
  animation: rotate 20s linear infinite;
}
@keyframes rotate {
  to {
    transform: rotate(360deg);
  }
}

#js {
  animation: scale-down 3s cubic-bezier(0.25, 0.46, 0.45, 0.94) both infinite;
}
@keyframes scale-down {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.5);
  }
}

#redux {
  animation: rotate-scale-down 5s linear both infinite;
}

@keyframes rotate-scale-down {
  0% {
    transform: scale(1) rotateZ(0);
  }
  50% {
    transform: scale(0.5) rotateZ(180deg);
  }
  100% {
    transform: scale(1) rotateZ(360deg);
  }
}

#python {
  animation: flip-horizontal-top 0.4s cubic-bezier(0.455, 0.03, 0.515, 0.955)
    both infinite;
}

@keyframes flip-horizontal-top {
  0% {
    transform: rotateX(0);
  }
  100% {
    transform: rotateX(180deg);
  }
}

#express {
  animation: swing-in-left-bck 10s cubic-bezier(0.175, 0.885, 0.32, 1.275) both
    infinite;
}
@keyframes swing-in-left-bck {
  0% {
    transform: rotateY(-70deg);
    transform-origin: left;
    opacity: 0;
  }
  100% {
    transform: rotateY(0);
    transform-origin: left;
    opacity: 1;
  }
}

#node {
  animation: heartbeat 1.5s ease-in-out infinite both;
}

@keyframes heartbeat {
  from {
    transform: scale(1);
    transform-origin: center center;
    animation-timing-function: ease-out;
  }
  10% {
    transform: scale(0.91);
    animation-timing-function: ease-in;
  }
  17% {
    transform: scale(0.98);
    animation-timing-function: ease-out;
  }
  33% {
    transform: scale(0.87);
    animation-timing-function: ease-in;
  }
  45% {
    transform: scale(1);
    animation-timing-function: ease-out;
  }
}

#mongo {
  animation: wobble 0.8s both infinite;
}
@keyframes wobble {
  0%,
  100% {
    transform: translateX(0%);
    transform-origin: 50% 50%;
  }
  15% {
    transform: translateX(-30px) rotate(-6deg);
  }
  30% {
    transform: translateX(15px) rotate(6deg);
  }
  45% {
    transform: translateX(-15px) rotate(-3.6deg);
  }
  60% {
    transform: translateX(9px) rotate(2.4deg);
  }
  75% {
    transform: translateX(-6px) rotate(-1.2deg);
  }
}

#sass-glass {
  animation: jello-horizontal 0.9s both infinite;
}

@keyframes jello-horizontal {
  0% {
    transform: scale3d(1, 1, 1);
  }
  30% {
    transform: scale3d(1.25, 0.75, 1);
  }
  40% {
    transform: scale3d(0.75, 1.25, 1);
  }
  50% {
    transform: scale3d(1.15, 0.85, 1);
  }
  65% {
    transform: scale3d(0.95, 1.05, 1);
  }
  75% {
    transform: scale3d(1.05, 0.95, 1);
  }
  100% {
    transform: scale3d(1, 1, 1);
  }
}

#sass {
  animation: swirl-out-bck 3s ease-in both infinite;
}

@keyframes swirl-out-bck {
  0% {
    transform: rotate(0) scale(1);
    opacity: 1;
  }
  100% {
    transform: rotate(-540deg) scale(0);
    opacity: 0;
  }
}

#html-text {
  animation: slide-out-tl 3s cubic-bezier(0.55, 0.085, 0.68, 0.53) both infinite;
}

@keyframes slide-out-tl {
  0% {
    transform: translateY(0) translateX(0);
    opacity: 1;
  }
  100% {
    transform: translateY(-50px) translateX(-50px);
    opacity: 0;
  }
}
#css-text {
  animation: slide-out-tr 3s cubic-bezier(0.55, 0.085, 0.68, 0.53) both infinite;
}
@keyframes slide-out-tr {
  0% {
    transform: translateY(0) translateX(0);
    opacity: 1;
  }
  100% {
    transform: translateY(-50px) translateX(50px);
    opacity: 0;
  }
}
#django {
  animation: swing-in-top-fwd 4s cubic-bezier(0.175, 0.885, 0.32, 1.275) both
    infinite;
}
@keyframes swing-in-top-fwd {
  0% {
    transform: rotateX(-100deg);
    transform-origin: top;
    opacity: 0;
  }
  100% {
    transform: rotateX(0deg);
    transform-origin: top;
    opacity: 1;
  }
}

#bootstrap-text {
  animation: vibrate-2 0.3s linear infinite both;
}
@keyframes vibrate-2 {
  0% {
    transform: translate(0);
  }
  20% {
    transform: translate(2px, -2px);
  }
  40% {
    transform: translate(2px, 2px);
  }
  60% {
    transform: translate(-2px, 2px);
  }
  80% {
    transform: translate(-2px, -2px);
  }
  100% {
    transform: translate(0);
  }
}

svg {
  &:hover {
    #react,
    #js,
    #html,
    #css,
    #sass,
    #bootstrap,
    #redux,
    #mongo,
    #express,
    #node,
    #python,
    #django,
    #js-test,
    #sass-glass,
    #css-text,
    #html-text,
    #bootstrap-text {
      animation: none;
    }
  }
}
