.message-me {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  cursor: pointer;
  border: 2px solid gold;
  #message-main {
    display: flex;
    width: 80%;
    height: 80%;
    transition: all linear 3s;
    &:hover {
      width: 75%;
      height: 75%;
      * {
        fill: blue;
      }
    }
  }
}
