.message-form {
  width: 80%;
  height: 80%;
  margin-top: 6%;
  margin-left: 10%;
  padding: 25px;
  box-shadow: 10px 10px rgba($color: #101010, $alpha: 0.8);
  background-color: rgba($color: #101010, $alpha: 0.7);
  @media (max-width: 560px) {
    display: none;
  }
  h2 {
    text-align: center;
    text-transform: uppercase;
    animation: messageBounce 3s infinite;

    img {
      width: 200px;
      height: 100px;
    }
  }
}

@keyframes messageBounce {
  0%,
  50%,
  100% {
    transform: translate3d(0, 0, 0);
  }

  10%,
  90% {
    transform: translate3d(0, -10px, 0);
  }
  20%,
  80% {
    transform: translate3d(0, 10px, 0);
  }
  30%,
  70% {
    transform: translate3d(-10px, 0px, 0);
  }
  40%,
  60% {
    transform: translate3d(10px, 0px, 0);
  }
  45% {
    transform: translate3d(0, 0px, -10px);
  }
  55% {
    transform: translate3d(10px, 0, 0);
  }
}
.thankYouForMessaging {
  text-align: center;
  font-size: 50px;
  width: auto;
  height: auto;
  position: absolute;
  color: black;
  top: 40%;
  left: 0%;
  margin: 0 200px;
  padding: 20px;
  text-transform: uppercase;
  font-weight: bold;
  background: rgba($color: floralwhite, $alpha: 0.7);
  box-shadow: 5px 5px 5px floralwhite;
  border: 5px solid wheat;
  border-radius: 1%;

  @media (max-width: 560px) {
    display: none;
  }
}
